body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (min-width: 768px) {
    nav {
        max-width: 100%; 
        height: 10px;
        margin-top: 5em;
        /* background-color: red; */
        display: flex; 
        align-items: center;
        justify-content: space-between;
    }
    .Logo {
        /* float: left; */
        margin-left: 5em;
        font-family: 'Julius Sans One', sans-serif;
        letter-spacing: 2px;
        /* background-color: red; */
        text-align: center;
    }
    .Links {
        margin-right: 5em;
        /* float: right; */
        /* background-color: red; */
    }
    .button {
        background-color: white;
        font-family: 'Khula', sans-serif;
        /* font-size: 1em; */
        margin: 15px;
        background-color: white;
        border: none;
        color: black;
        outline: none;
        position: relative;
    }
    .button:hover{
        color: #739652;
      }
    .button:before,
    .button:after {
    border: 0 solid transparent;
    transition: all 0.25s;
    content: '';
    height: 24px;
    position: absolute;
    width: 0px;
    }
    .button:before {
        border-bottom: 1.5px solid #818a91;
        left: 0px;
        top: -5px;
    }
      .button:hover:before,
      .button:hover:after {
        height: 100%;
        width: 100%;
      }

}
@media (min-width: 768px) {
    .landing-all {
        max-width: 100%;
        margin: 0 5em 0 5em;
        /* background-color: blue; */
    }
    .home {
        max-width: 100%; 
        height: 80vh;
        /* margin: 0 5em 0 5em; */
        background: url(/static/media/Photo1.7a4aed17.PNG) no-repeat center center fixed; 
        background-size: cover; 
        /* display: flex; */
        /* justify-content: space-between; */
    }
    shadow {
    display:block;
    position:relative;
    }
    
    .Heading {
        padding: 100px;
        font-family: 'Nanum Myeongjo', serif;
        font-size: 40px;
        /* background-color: blue;    */
    }

    .About {
        padding: 80px;
        
        text-align: center;
        font-family: 'Julius Sans One', sans-serif;

    }

    .About h2{
        vertical-align: middle;
        text-transform: uppercase;
        line-height: 1em;
        text-align: center;
        display: inline-block;
        letter-spacing: -1px;
    }
    .About h2::after{
        content: "";
        display: block;
        width: 70%;
        height: 1px;
        margin: auto;
        margin-top: 10px;
        background-color: black;
    }
    .About h3{
        text-transform: uppercase;
    }

    #yogateacher {
        display: flex; 
        justify-content: space-evenly;
        font-family: 'Khula', sans-serif;
        padding-top: 10px;
    }
    #yogateacher .container {
        width: 400px;
        /* background-color: blue; */
    }
    

    
}


@media (min-width: 768px) {
    .Footer {
        max-width: 100%; 
        height: 70px;
        margin: 1em 5em 5em 5em;
        /* background-color: yellow; */
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-top: 0.5px solid #818a91; 
        /* opacity:0.5 */
    }
    .other-links{
      list-style: none;
      padding: 0;
      display: flex;
      /* background-color: red; */
      font-family: 'Khula', sans-serif;
      color: #818a91;
    }
    .other-links li {
      padding: 0.4em;
    }
    .other-links a {
      text-decoration: none;
    }
    
    .social-icons {
        list-style:none;
        display: flex;
        
      }
      .social-icons a{
        background:lightgray;
        color:#818a91;
        font-size:20px;
        display:inline-block;
        line-height:44px;
        width:44px;
        height:44px;
        text-align:center;
        margin-right:8px;
        border-radius:100%;
        transition:all .2s linear;
      }
      .social-icons a:active,.social-icons a:focus,.social-icons a:hover {
        color:#fff;
        background-color:#739652;
      }
      .social-icons a.instagram:hover {  
        background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
        }
      .other-links a:hover{
        color: #739652;
      }

        .social-icons a.email:hover {  
          background: #739652;
          }

}

@media (min-width: 768px) {
  .studio {
    max-width: 100%;
    margin: 2.5em 5em 0 5em;
    background-color: rgb(249,236,224, 0.5);
    height: 80vh;
    
  }
  .coming-soon-text {
    padding-top: 5em;
  }
  .soon {
    font-size: 28px;
  }
  .studio-container {
    text-align: center;
    padding-left: 0.5em;
    padding-top: 2em;
    font-family: 'Julius Sans One', sans-serif;
    color: #343634; 
  }

  .studio h2{
    vertical-align: middle;
    line-height: 1em;
    text-align: center;
    display: inline-block;
}
.studio h2::after{
    content: "";
    display: block;
    width: 70%;
    height: 1px;
    margin: auto;
    margin-top: 10px;
    background-color: #343634;
}

}

